import React from 'react'

import logo from './assets/noku-logo.png';



const Navbar = () => {
  
    return (
  <div className="nav-bar bar mui-sticky navBar">  
   <img  src={logo} className="logo2" alt="logo2"  width="100" height="100"/>
                <ul className="nav-links ">
                <input type="checkbox" className='toggleNav'/>
                <label className='nav-bar-toggle' htmlFor="toggleNav">
                <i className="material-icons">menu</i></label>
                 <li className="nav-item b-line"><a href="#home">home</a></li>
                <li className="nav-item b-line "> <a href="#work">work</a>
              </li>
                <li className="nav-item b-line">about</li>
                <li className="nav-item b-line last-li">contact</li>
                
             </ul>
                </div>
      
    )
}

export default Navbar
